img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;

  margin: -1px;
  width: 1px;
  height: 1px;

  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;

  &:focus {
    outline: none;
  }
}
